@media screen and (min-width: 270px) {
  .footer {
    grid-column: 1/2;
    grid-row: 3/4;
    background-color: #424242;
  }
}

@media screen and (min-width: 700px) {
  .footer {
    grid-column: 1/10;
  }
}

@media screen and (min-width: 1024px) {
  .footer {
    grid-column: 1/13;
  }
}
